import React from 'react'
import { Link } from 'react-router-dom'

const Home = () => {
    return (
        <div className="col-md-10 mx-auto mt-5">
            <div className="jumbotron">
                <h1 className="display-3">Hello, world!</h1>
                <p className="lead">Thn tulisi tehd sovelluksen etusivu.</p>
                <hr className="my-4" />
                <p>Se voi kuvailla projektin ideaa, syit miksi se on tehty, mit sill voi tehd ja mit sen toteutuksessa on kytetty. Voisin laittaa vaikka linkin mys kirjautumiseen ja ehk mys rekisteritymiseen.</p>
                <hr/>
                <Link to="/login" className="btn btn-outline-primary">Kirjaudu</Link>
            </div>
        </div>
    )
}
export default Home
