import React, { useState, useEffect } from 'react'
import { TextEditor } from '../Document/TextEditor';
import request from '../Shared/HttpRequests'

const KokousPoytakirja = ({ kokous, yhdistys, osallistujat, puheenjohtaja, paatokset }) => {

    const kaikki = puheenjohtaja.concat(osallistujat)
    const avannutEsityslistan = kaikki.map(x => x.pv_avannut).reduce((a, v) => (v === "1" ? a + 1 : a), 0)
    const ollutAktiivinen = kaikki.map(x => x.pv_aktiivinen).reduce((a, v) => (v === '1' ? a + 1 : a), 0)

    const pvmForm = { month: 'numeric', day: 'numeric', year: 'numeric' };
    const pvmYear = { year: 'numeric' }
    const kokousnumero = kokous.kokousnro + "/" + (new Date(kokous.startDate)).toLocaleDateString('fi-FI', pvmYear)
    const otsikko = kokous.otsikko !== '' ? `<h2>${kokous.otsikko}</h2>` : ''
    const ajat = (new Date(kokous.startDate)).toLocaleDateString('fi-FI', pvmForm) + " - " + (new Date(kokous.endDate)).toLocaleDateString('fi-FI', pvmForm)
    const osallistuu = osallistujat.map(x => '<li>' + x.firstname + ' ' + x.lastname + '</li>').join(' ')
    let paatosvalta = ''
    if (kokous.pv_aktiivisuus === '0' && kokous.pv_esityslista === '0' && kokous.pv_kesto === '0' && kokous.pv_muu === '') paatosvalta += '<p>Kokouksen ptsvaltaisuutta ei ole mritelty.</p>'
    else { // TODO keston lisksi muut ptsvalta kriteerit !!! ---------------------------
        if (kokous.pv_esityslista !== '0') paatosvalta += kokous.pv_esityslista <= avannutEsityslistan ? `Kokouksen asialistan avasi vaadittu ${kokous.pv_esityslista} kpl osallistujista.` : `<p>Kokouksen esityslistaa ei avannut riittv mr (${kokous.pv_esityslista} kpl) osallistujista.</p>`
        if (kokous.pv_aktiivisuus !== '0') paatosvalta += kokous.pv_aktiivisuus <= ollutAktiivinen ?  `<p>Kokousosallistujista ${kokous.pv_aktiivisuus} kpl on ottanut kantaa.</p>` : `<p>Kokouksen osallistujista ei ollut riittv mr (${kokous.pv_aktiivisuus} kpl) aktiivisia.</p>`
        if (kokous.pv_kesto !== '0') paatosvalta += kokous.pv_kesto_toteutunut === "true" ? `<p>Kokoukselle mritelty minimikesto ${kokous.pv_kesto} vuorokautta toteutui.</p>` : `<p>Kokoukselle mritelty minimikesto ${kokous.pv_kesto} vuorokautta ei toteutunut.</p>`
        if (kokous.pv_muu !== '') paatosvalta += `<p>${kokous.pv_muu}</p>`
    }
    paatokset = paatokset.filter(x => x.tila === "3" || x.tila === "23")
    const paatostiedot = paatokset.length !== 0 ? paatokset.map(x => x.paatos !== '' ? '<h3>' + x.title + '</h3>' + '<p style="margin-top: -10px;">' + x.paatos + '</p>' : '<h3>' + x.title + '</h3>').join(' ') : 'Kokouksessa ei tehty ptksi.'
    const pj = puheenjohtaja.length !== 0 ? puheenjohtaja[0].firstname + " " + puheenjohtaja[0].lastname : "kokouksessa ei ole puheenjohtajaa"
    const [poytakirja, setPoytakirja] = useState(`<h1>${yhdistys}</h1>${otsikko}<h2>Kokous ${kokousnumero}</h2><h2>${ajat}</h2><h2>Osallistujat</h2><ul>${osallistuu}</ul><p>Puheenjohtaja: ${pj}<h2>Ptsvaltaisuus</h2>${paatosvalta}<h2>Asiakohdat</h2>${paatostiedot}`)

    const editorContentChange = (poytakirja) => {
        setPoytakirja(poytakirja)
    }
    const handlePaataKokousClick = () => {
        if (window.confirm('Haluatko ptt kokouksen? Tietojen muuttaminen ei ole en kokouksen ptettysi mahdollista')) {
            const body = JSON.stringify({ call: 'paatakokous', kokousid: kokous.id })
            request.kokous(body).then(res => {
                savePoytakirja()
            }).catch(err => console.log('err.response', err.response))
        }
    }
    const savePoytakirja = () => {

        const body = JSON.stringify({ call: 'postdoc', id_y: kokous.id_y, kokousnro: kokous.kokousnro, type: 'poytakirja', content: poytakirja, draft: 0 })
        request.documents(body).then(res => {
            alert("Kokous on ptetty ja pytkirja tallennettu!")
            window.location.reload()
        }).catch(err => console.log('err.response', err.response))

    }
    if (poytakirja) {
        return (
            <div className="mt-5 mx-auto col-md-10">
                <TextEditor editorContentChange={editorContentChange} teksti={poytakirja} className="mb-2" />
                <button className="float-right btn btn-outline-primary mt-1" onClick={() => handlePaataKokousClick()}>Pt kokous ja tallenna pytkirja</button>
                <br></br>
            </div>
        )
    } else {
        return <p>Loading....</p>
    }
}

export default KokousPoytakirja

// Pytkirjaan tulostuvat automaattisesti:
//  Yhdistyksen nimi
//  Kokouksen jrjestysnumero / vuosi
//  Kokousajat
//  Kokouksen osallistujat
//  Kokouksen ptsvaltaisuus
//  Asiakohtien otsikot
//  Asiakohtien sisltkentt 

